.container {
    display: flex;
    height: calc(100% - 5px);
    background: #f7f7f7;
}

.column {
    display: inline-block;
    text-align: "center";
    width: 320px;
    height: 80vh;
}

.columnTitle {
    color: #383838;
    font-size: 14px;
    font-weight: 600;
    line-height: 40px;
    margin: 0 10px;
}

.columnContent {
    height: calc(100% - 5px);
    overflow: auto;
}

.columnContentActive {
    overflow: auto;
    height: calc(100% - 5px);
    background: #ccecff;
    border: 2px solid #1b9aee;
}

.issue {
    text-align: center;
    position: relative;
    min-height: 20px;
    padding: 15px 15px;
    background: #ffffff;
    // margin: 8px;
}

.issueDragging {
    text-align: center;
    position: relative;
    min-height: 20px;
    padding: 14px;
    background: #ffffff;
    opacity: 0.9;
    // margin: 8px;
}
@primary-color: #1DA57A;